import 'vux/src/styles/reset.less';
import './member_seeking_books.scss'
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'


import { fetch, rap } from 'js/fetch.js'


let url = {
getRecommendBooks: 'api/book/recommends',//用户推荐的图书列表
recommends: 'api/book/recommends'
}


url = rap(url)

import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'
import Booklist from 'components/booklist/booklist.vue'

import utils from 'js/utils.js'

import mixin from 'js/mixin.js'


new Vue({
el: '#body',
data: {
title: '',//书名
author: '',//作者
press: '',//出版社
showList1: true,
bookList:[],
scrollTop: 0,
onFetching: false,
bottomCount: 20,
n4: 10,
demo4Value: {
pullupStatus: 'default'
}
},
computed: {
isDisabled() {

if (this.title&&this.author&&this.press) {
return false
} else {
return true
}
}
},
created() {
this.isLogin = utils.isLogin()


if (!this.isLogin) {
window.location.href = './login.html'
} else {
this.token = cookie.get('token')
this.getRecommendBooks()
}



},
methods: {
recommendBook() {
let _this = this

fetch('post', `${url.recommends}`, { name: this.title, author: this.author, press: this.press }, { 'headers': _this.token }).then(res => {
if (res.status >= 200 && res.status <= 300) {
_this.$vux.toast.show({
text: res.data.message,
onShow() {

},
onHide() {
window.location.href=window.location.href

}
})
} else {
_this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
},
getRecommendBooks(){

fetch('get', `${url.getRecommendBooks}`,{}, { 'headers': this.token }).then(res => {
if (res.status >= 200 && res.status <= 300) {
this.$set(this, 'bookList', res.data);
} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
}
},
components: {
Spinner,
Badge,
Group,
Cell,
Panel,
XButton,
Scroller,
Cell,
XInput,
Booklist
},
mixins: [mixin]
})



